.oct--detail--content.oct--about-us {
  background-color: var(--oct-home-column-3n1-bg-color);
}

.oct--detail--content.oct--activities {
  background-color: var(--oct-home-column-3n2-bg-color);
}

.oct--detail--content.oct--services {
  background-color: var(--oct-home-column-3n3-bg-color);
}

.oct-box-columns > .oct--detail--content{
  -ms-flex: 1 1;
      flex: 1 1;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 25% 75%;
  padding: 5vh;
  color: #fafafa;
  border-radius: 5px;
}

.oct--detail--content .oct--detail--title {
  -ms-flex-item-align: end;
      align-self: end;
  font-size: 13vh;
}

.oct--detail--content .oct--detail--body {
  font-size: 5vh;
}

.oct--detail--content hr {
  margin-top: 0;
}



