.oct--home-body--column:nth-child(3n+1) {
  background-color: var(--oct-home-column-3n1-bg-color);
}
.oct--home-body--column:nth-child(3n+2) {
  background-color: var(--oct-home-column-3n2-bg-color);
}
.oct--home-body--column:nth-child(3n+3) {
  background-color: var(--oct-home-column-3n3-bg-color);
}

.oct--home-body--column {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-transition: 0.3s linear, color 0.2s linear;
  -o-transition: 0.3s linear, color 0.2s linear;
  transition: 0.3s linear, color 0.2s linear;
  color: #fff !important;
}

.oct--home-body--column.oct--home-body--column--hovered {
  background-color: #fafafa;
  color: #333;
}

.oct--home-body--column-title--hovered {
  color: #333;
}

.oct--home-body--column-title {
  -webkit-transition: 0.2s linear;
  -o-transition: 0.2s linear;
  transition: 0.2s linear;
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: center;
  font-size: 4.5vw;
  padding: 0 2vw;
  position: relative;
}

@media (min-width: 769px) {
  .oct--home-body--column:not(:first-of-type) {
    border-left: 2px solid #ddd;
  }

  .oct--home-body--column {
    border-radius: 5px;
  }
}

@media (max-width: 769px), (orientation: portrait)  {
  .oct--home-body--column-title {
    -webkit-transition: 0.2s linear;
    -o-transition: 0.2s linear;
    transition: 0.2s linear;
    text-align: center;
    font-size: 6vmax;
    padding: 0 2vw;
  }
}



